.pop-up {
  display: flex;
  justify-items: center;
  //   flex-direction: column;
  align-items: center;
  padding: 2.5em 8em;
  margin: auto;
  font: normal 16px/30px Inter, sans-serif;
  text-align: center;
  .modal-content {
    // background-color: brown;
    width: 35em;
    align-self: center;
    margin: auto;
    display: flex;
    flex-direction: column;
    .modal-header {
      border-bottom: none;
      text-align: center;
      justify-content: center;
      .modal-title {
        text-align: center;
        font-weight: bold;
      }
    }
    .modal-body {
      padding: 0 2em 2em 2em;
      //   display: flex;/

      .pop-content {
        div {
          display: flex;
          padding: 10px;
          .socials {
            display: flex;
            flex-direction: column;
            a {
              span {
                text-decoration: none;
                color: black;
              }
            }
          }
        }
      }
    }
  }
}
