.we-support {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h2 {
    margin: 0 auto 30px auto;
  }

  .photos {
    width: 100%;
    padding: 0 16px 46px 16px;

    display: flex;
    flex-wrap: nowrap;
    gap: 16px;

    overflow-x: auto;

    .photo-container::before {
      content: "";

      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;

      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 3.12%,
        rgba(0, 0, 0, 0.75) 100%
      );
    }

    .photo-container {
      width: 150px;
      height: 195px;
      border-radius: 8px;
      overflow: hidden;
      cursor: pointer;
      flex: 0 0 auto;

      color: var(--light);

      position: relative;

      img {
        position: absolute;
        // inset: 0;
        z-index: 1;
      }

      h4 {
        position: absolute;
        inset: auto auto 10.59px 10.59px;
        z-index: 3;

        // FONT SHORTHAND: style, variant, weight, size/line-hight, family
        font: normal bold 16px/19px Roboto, sans-serif;
      }
    }

    // ================= IMAGE PLACING START ==================
    .healthcare {
      img {
        width: 297px;
        inset: 0 0 auto auto;
      }
    }

    .civil-rights {
      img {
        width: 294px;
        inset: 0 -43% auto auto;
      }
    }

    .employment {
      img {
        width: 338px;
        inset: -5% auto auto -49%;
      }
    }

    .economy {
      img {
        width: 182px;
        inset: 0 auto auto -6%;
      }
    }

    .environment {
      img {
        width: 328px;
        inset: -10% auto auto -57%;
      }
    }

    .poverty {
      img {
        width: 437px;
        inset: -28% auto auto -96%;
      }
    }

    .education {
      img {
        width: 394px;
        inset: auto auto -3% -49%;
      }
    }

    .childrens-rights {
      img {
        width: 428px;
        inset: -36% auto auto -101%;
      }
    }

    .animals {
      img {
        width: 184px;
        inset: -24% 0 auto auto;
      }
    }

    .emergency-safety {
      img {
        width: 150px;
        inset: auto auto 0 0;
      }
    }

    .justice-legal {
      img {
        width: 294px;
        inset: 0 auto auto -51%;
      }
    }

    .technology {
      img {
        width: 454px;
        inset: -30% auto auto -78%;
      }
    }

    .art-culture {
      img {
        width: 293px;
        inset: 0 auto auto -44%;
      }
    }

    .sports-recreation {
      img {
        width: 380px;
        inset: auto auto 0 -88%;
      }
    }

    .other-help {
      img {
        width: 306px;
        inset: 0 auto auto -23%;
      }
    }

    // ================= IMAGE PLACING END ====================
  }
}

@media screen and (min-width: 576px) {
  .we-support {
    h2 {
      font: normal bold 48px/56px Roboto, sans-serif;

      margin: 56px auto 53px auto;
    }
  }
}

@media screen and (min-width: 1025px) {
  .we-support {
    h2 {
      // FONT SHORTHAND: style, variant, weight, size/line-hight, family
      font: normal bold 48px/56px Roboto, sans-serif;

      margin: 98px auto 57px auto;
    }
  }
}

